import React, { useState } from 'react';

function App() {
  const [age, setAge] = useState(0);
  const [year, setYear] = useState(1981);

  function calculateAge() {
    const currentYear = new Date().getFullYear();
    setAge(currentYear - year);
  }

  function handleChange(event) {
    setYear(event.target.value);
  }
  
  return (
    <div>
      <h1>What year were you born?</h1>
      <div>
        <input type="number" placeholder="Birth Year" value={year} onChange={handleChange} />
        <button type="button" onClick={calculateAge}>Submit</button>
      </div>
      <p>
        { age ? <p>You are {age} years old</p> : null }
      </p>
    </div>
  )
}

export default App;
